.page-loader-main {
    height: 750px;
    row-gap: 90px;
}

.page-loader-cost {
    row-gap: 55px;
}

.page-loader-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #3e3c41;
}

.page-loader-desc {
    width: 100%;
    display: block;

    text-align: center;

    color: var(--secondary);
    text-transform: uppercase;
}

.animation-area {
    width: 300px;
    background-color: transparent;
    position: relative;
}

.train-container {
    position: relative;
    background-image: url('../../../../../img/trainPng.png');
    height: 25px;

    width: 290px;
    background-size: 290px;
    top: 0;
    right: 0;
    margin: auto;
    transform: rotate(360deg);
    animation-name: trainbefore;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
}

@keyframes trainbefore {
    from {
        background-position: 0px;
    }
    to {
        background-position: 290px;
    }
}
